
import Api from '../dal/api';
const   TOGGELISFETCHING = 'toggelIsFetching'
const GETCARTBALANS = 'GetCartBalans'
const PUTCARTBALANS = 'PutCartBalans'
const POSTCARTBALANS = 'PostCartBalans'

export type initStateTyp = {
    cartID: string,
    balans: number | null,
    isFetching : boolean,
    viewingCart:boolean,


 }


let initState:initStateTyp = {
    cartID: '',
    balans: null,
    isFetching : false,
    viewingCart:false
    
}

export let CartReducer = (state = initState ,action:any ) =>{
    
    debugger
    switch(action.type){
        case GETCARTBALANS :
            return{
                cartID:action.cart,
                balans:action.balans,
                viewingCart:true,
            }

          case TOGGELISFETCHING:
            return{
                ...state,
                isFetching: action.IsFetching

            }   

            case POSTCARTBALANS :
            return{
               balans:action.balans
            }
     default:
         return state
    }
    
}



type actionGetCartType ={
    type: typeof GETCARTBALANS,
    cart:string,
    balans:number,
    viewingCart:boolean

}
type toggelIsFetchingType ={
    type: typeof TOGGELISFETCHING,
    IsFetching: boolean
}
export let GetCart = (cart:string,balans:number,viewingCart:boolean):actionGetCartType =>{
    
  return { 
    type:GETCARTBALANS,
    cart,
    balans,
    viewingCart
}
}
export let putCart = (balans:number) =>{
    
    return { 
      type:GETCARTBALANS,
      balans,
      
  }
  }
  export let postCart = (balans:number) =>{
    
    return { 
      type:POSTCARTBALANS,
      balans,
      
  }
  }

export let toggelIsFetching = (IsFetching:boolean):toggelIsFetchingType =>{
    
    return { 
    type:TOGGELISFETCHING,
    IsFetching
    }
    
}


export const GetCartAPI = (cartID:any) => async(dispatch:any) => {
   
    dispatch(toggelIsFetching(true))
    
   let data = await Api.getCartAPI(cartID)
   
  
   if(data.Status === 'green'){
    dispatch(GetCart(cartID,data.balans,true))
       localStorage.setItem('cart',cartID);
    }
    dispatch(toggelIsFetching(false))
   
    
}

export const PutCartAPI =  () => async(dispatch:any) => {
    dispatch(toggelIsFetching(true))
    let data = await Api.putBalansCartAPI()
    if(data.Status === 'green'){
        let cart:any = localStorage.getItem('cart');
        dispatch(GetCart(cart,data.balans,true))
        }
    dispatch(toggelIsFetching(false))
   
}

export const PostCartAPI =  (balans:number) => async(dispatch:any) => {
    dispatch(toggelIsFetching(true))
    let data = await Api.postBalansCartAPI(balans)
    console.log(data)
        let cart:any = localStorage.getItem('cart');
        if(data.Status === 'green'){
            let cart:any = localStorage.getItem('cart');
            dispatch(GetCart(cart,data.balans,true))
            }
            dispatch(toggelIsFetching(false))
   
  
   
}
