import React, { useRef, useState,useEffect  } from 'react';
import Header from './Header';
import { reduxForm, Field } from 'redux-form' // imported Fields
import inputCart from './inputCart'
import style from './inpCart.module.css'
import './Eticket.css';
import img from '../img/high-speed-train.svg'
import preloader from '../img/Eclipse-2.2s-200px.svg'
import Preloader from './preloader'
import PreloaderMini from './preloaderMini'
import './preloader.css'


let EticketForm: any = (props: any) => {
 
  const notification: any = useRef(null);
  const CartBalans: any = useRef(null);
  const [timeEditMod, setTimeEditMod] = useState(false)
  const [editMod, setEditMod] = useState(false)
  const [ValueInputBalansCart, setValueInputBalansCart]:any = useState(null)


  let editModFanc = (): any => {
    setEditMod(true )
    editMod && setEditMod(false)
  }
  let hidden = (): any => notification.current.classList.toggle("hidden")
  let changeInputValue = (e:any):any  =>{
  setValueInputBalansCart(e.currentTarget.value )
    
  }

let onBlurFanc = () =>{
  if(ValueInputBalansCart <= 500){
    props.PostCartAPI(ValueInputBalansCart)
  }else {
    
  }
    
  editModFanc()
}

let PutCart = () =>{
  if (props.balans >= 8){
    props.PutCartAPI()
  }
  else{
    return  0
  }
  
}




  const { handleSubmit } = props
  if (props.viewingCart) {
    return <>
      <span className="Etiket__text">Eticket</span>
      <form onSubmit={handleSubmit}>
        <div className={style.Cart}>{props.Cart}</div>
        {
          editMod ?
            <div className={style.balansInputWrapper}>
              <input ref={CartBalans} min="0" value={ValueInputBalansCart} defaultValue={props.balans} onChange={changeInputValue} max='500' onBlur={onBlurFanc} className={style.balansInput}  type='number' />
              <span>UAH</span>
            </div>
            :
            <div onClick={editModFanc}  className={style.balans}>{props.balans}
              <span>UAH</span>
            </div>
        }
        {props.isFetching && 
        <PreloaderMini/>

        }
      </form>
      <button onClick={PutCart} className={style.travel}>
        <span>Travel</span>
      </button>
      {editMod && <div onClick={hidden} ref={notification} className="notification__Editmod"> <span>Balance change</span>   </div>}
    </>

  } else if (props.isFetching === true) {

    return <div className={style.loading}>
      <Preloader />
    </div>
  }
  else {
    return <>
      <span className="Etiket__text">Eticket</span>
      <form onSubmit={handleSubmit}>
        <Field name="ticket" component={inputCart} />
        <button className="Etiket__search"><span>Search</span></button>
      </form>
    </>
  }

}
const EticketFormRedux = reduxForm({
  // a unique name for the form
  form: 'ticket'

})(EticketForm)

const Eticket = (props: any) => {

  const onSubmit = (formData: any) => {
    try {
      let cartID = formData.ticket.toUpperCase()
      props.GetCartAPI(cartID)
    } catch { }
  }
  //console.log(inputEl.current.value.toUpperCase())

  return (

    <div className="wraper_Etiket__Page">
      <Header />
      <div className="wraper_Etiket">
        <div className="Etiket">
          <div className="Cart">
            <div className="Hidden"><img src={preloader} alt="" /></div>
            <EticketFormRedux {...props} onSubmit={onSubmit} />

          </div>
        </div>
      </div>

    </div>)
}

export default Eticket