import React from 'react';
import Eticket from './Eticket';
import { compose } from 'redux'
import { connect } from 'react-redux';
import {GetCart,GetCartAPI,PutCartAPI,PostCartAPI }from '../bll/reducer'




let EticketContainer = (props:any):any =>{


    return <Eticket {...props} />

    
   
}
let mapStateToProps =(state:any)=>{
  
  
    return {
        Cart:state.Cart.cartID,
        viewingCart:state.Cart.viewingCart,
        balans:state.Cart.balans,
        isFetching:state.Cart.isFetching
    }
}
export default  connect(mapStateToProps,
       { GetCart,
        GetCartAPI,
        PutCartAPI,
        PostCartAPI}
        )(EticketContainer)
