const { default: axios } = require('axios');
let cartLocal:any = localStorage.getItem('cart');
export const Api:any = {
    getCartAPI(idCart:string ) { 
        return axios.get(`https://apieticket.herokuapp.com/getCart?cart=${idCart}`).then((res:any) => {
            return res.data
        })
    },
    putBalansCartAPI(balans:number = 8 ) {
        
        return axios.put(`https://apieticket.herokuapp.com/putBalansCart`,{
            "0436662A5F6A80" : {
            "balans": balans
          }
        }, {
            headers: {'Content-Type': 'application/json'}
        }).then((res:any) => {
            return res.data
        })

    },
    postBalansCartAPI(balans:number ) {
        
        return axios.post(`https://apieticket.herokuapp.com/postEditBalansCart`,{
            "0436662A5F6A80" : {
            "balans": balans
          }
        }, {
            headers: {'Content-Type': 'application/json'}
        }).then((res:any) => {
           return res.data
        })

    }

}

   
export default Api